#article{
    display: flex;
    justify-content:space-between;
    justify-content:center;
}

#article img{
    width:100%;

    border-radius: 10px;
}

.tBody{
    margin-bottom: 0px;
    padding: 40px 100px;
    background-color: hsl(0, 0%, 98%);
}
.tBody .h{
    margin: 30px 0px;
    font-size: 30px;
   
}

.aBody{
    margin-right:30px;
    background-color: white;
    border-radius:5px;
}
.aBody .pname{
  font-size: 8px;
}

.aBody p{
    margin: 5px 0px;
    font-size: 12px;
    line-height:18px;
  }

.aBody p, .aBody h1{
    padding:0px 15px 10px 15px;
}
  
@media (max-width: 550px){
    #article{
        flex-direction: column;
        align-items: center;
    }
    .tBody .h{
        text-align: center;
        margin-top: 10px;
       
    }
    .tBody{
        text-align: left;
        padding: 30px 5px;
        margin: 0px auto;
       
    }
    .aBody{
        margin: 20px 0px;
        width:75%;
    }
    .aBody .boxImage{
        margin-top: 0px;
        max-width:100%;
        
    }

    .aBody p, .aBody h1{
        padding:0px 15px 10px 15px;
    }
    
  }

@media (min-width: 550px) and (max-width: 1000px){
    #article{
        flex-direction: column;
        align-items: center;
    }
    #arcticle img{
        height:100px;
    }
    .tBody .h{
        text-align: center;
        margin-top: 10px;
       
    }
    .tBody{
        text-align: left;
        padding: 40px 40px;
        margin: 0px auto;
       
    }
    .aBody{
        margin: 20px 0px;
        width:75%;
    }
    .aBody .boxImage{
        margin-top: 0px;
        max-width:100%;   
    }

    .aBody p, .aBody h1{
        padding:0px 15px 10px 15px;
    }
}
