.nav{
    display:flex;
    justify-content:space-between;
    justify-items: center;
    align-items: center;
    padding:10px 120px;
    color:rgb(141, 133, 133);
    font-weight: 900;
    
}
.nav .middle span{
    margin-right:20px;  
}
.nav .middle span:hover{
    color:black;
}
.nav .middle .top span{
    font-size: 15px;
}

.btn{
   padding:10px 25px;
   border: none;
   border-radius: 25px;
   font-size:10px;
   background: linear-gradient(to right, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
   color:hsl(0, 0%, 98%);
}
.btn:hover{
    opacity: 0.55;
}

.nav .top{
    display:flex;
    justify-content: space-between;
    justify-items: center;
     align-items:center;
}

.nav .top .line{
    border: 2px solid green;
    margin-right:20px;
    margin-top:30px;
    background-color: green;
    border-radius:10px;
    display:none;
}
.nav .fold{
    display: none;
}
@media (max-width: 900px){
   .nav .middle {
      display:none;
   }
   .nav .right{
       display:none;
   }
   .nav{
       margin-top: 15px;
       margin-left: 0px;
       padding: 5px 30px;
   }
   .nav .fold{
       display: block;
       width: 30px;
   }
   .nav .fold div{ 
       margin-bottom:4px;
       width:auto;
       height:2px;
       background-color: hsl(136, 65%, 51%);
   }
   

  }

@media (min-width: 900px) and (max-width: 1100px){
    .nav .middle .top span{
        font-size: 12px;
   }
   .btn{
    padding:8px 18px;
    
 }
}

@media (min-width: 1100px) and (max-width: 1400px){
    .nav .middle .top span{
        font-size: 14px;
   }
   .btn{
    padding:9px 20px;
    
 }
}