.drop{
    text-align: center;
    margin-top:80px;
    line-height: 35px;
    padding-top:10px;
    margin-left:-20px;
}
.drop p{
    font-size: 14px;
}
.close{
    float:right;
    background-color: hsl(136, 65%, 51%);
    border: none;
    color:white;
    font-size: 25px; 
    margin-right:60px;
}
.close:hover{
    font-size: 30px;
}

.drop p:hover{
    font-size: 16px;
    color: rgb(241, 238, 238);
}

@media screen and (max-width: 400px) {
    .close{
      margin-right:30px;
    }
}