.foot{
    padding: 50px 130px;
    background-color:hsl(233, 26%, 24%);
    display:flex;
    justify-content:space-between;
    justify-items: center;
    align-items:center;
    color:white;
    bottom:0;
}
.foot .right1 i{
    margin:10px 5px;
    padding:2px;
    background-color: white;
    color:hsl(233, 26%, 24%);
    border-radius:50%;
}
.foot .right1 i:hover{
   color: hsl(136, 65%, 51%);
   background-color: hsl(233, 26%, 24%);
}
.foot .middle{
    display:flex;
    justify-content:space-between;
    justify-items: center;
    align-items:center;
}
.foot .middle .left2 ul,
.foot .middle .right2 ul{
  list-style-type:none;
  line-height: 25px;
  margin-right:100px;
}
.foot .middle li{
    font-size:12px;
}
.foot .middle li:hover{
    font-size:12px;
    color: hsl(136, 65%, 51%);
}

.foot .right{
    margin-right:130px;
}
.foot .right .btn{
  margin-bottom:20px;
}
.foot .right p{
    font-size: 10px;
}

@media (max-width: 900px) {
    .foot {   
      flex-direction: column;
      text-align: center;
      align-items: center;
      padding-bottom:5px;
    }
    .foot .left{
        margin-bottom:25px;
    }
    .foot .middle{
        margin-bottom:30px;
        flex-direction: column;
        align-items: center;
    }
    .foot .middle .left2 ul,
    .foot .middle .right2 ul{
     margin-right:40px;
    }  
.foot .right{
    margin-right:0px;
}
.foot .right p{
    font-size: 7px;
}
.foot .right1 i{
    margin:10px 2px;
}
  }

  @media (min-width: 900px) and (max-width: 1200px){
    .foot .right1 i{
        margin:10px 3px;
    }
    .foot .middle .left2 ul,
.foot .middle .right2 ul{
  margin-right:10px;
}
.foot .right{
    margin-right:-50px;
}
  }