.sec{
    background-color:hsl(220, 16%, 96%);
    padding: 40px 0px 60px 130px;
}

.sec #services{
    display:flex;
    justify-content: space-between;
    justify-items: center;
}

.sec .services{
     margin-top:50px;
     width:300px;
}

.sec .shead{
    width:44%;
}
.sec .services .bhead{
    margin-right:20px;
}
.sec .services .bhead h1{
    margin-right:20px;
    margin: 20px 0px;
}

.sec .shead h1{
    margin-bottom:20px;
    margin-top:10px;
    font-size:30px;
}
.sec .shead p{
    line-height: 25px;
}

.sec .services p{
    font-size:12px;
    margin-bottom:30px;
    line-height: 20px;
}

@media (max-width: 900px){
    .sec #services{
       flex-direction: column;
       width:250px;
       align-items:center;
       margin:auto;
    }
    .sec{
        align-items: center;
        text-align: center;
        padding:30px 30px;
        margin:auto;
    }
    .sec .shead{
        width:80%;
        margin: 0px auto;
    }
    
  }


