.firstB{
    display: flex;
    justify-content: space-between;
    justify-items: center;
    align-items:center;
    background-color: hsl(0, 0%, 98%);
    background-position: center;
    overflow: hidden;
    width: 100%;
    height:500px;
    margin:auto;
}

.firstB .fright{
    background: url('../images/backg.svg');
    margin-top:70px;
    width:auto;
    padding:200px;
}

.firstB .fright img{
    margin-top:-30px;
    height: 700px;
    width:auto;
    margin-right:-100px;
}

.firstB .left{
    padding: 0px 0px 0px 140px;
}

.firstB .left h1{
    font-size:55px;
}

.firstB .left .btn{
    margin-top:20px;
    margin-bottom: 20px;
}
.firstB .left p{
    line-height:25px;
    margin-top:20px;
}

@media (max-width: 900px){
    .firstB{
        flex-direction: column-reverse;
        text-align:center;
        align-items: center;
        height:600px;
    }
    .firstB .fright{
        background: url('../images/bg-intro-mobile.svg');
        width:100%;

}
.firstB .left h1{
    font-size:34px;
    width:90%;
    margin: 0px auto;
}
.firstB .fright img{
    height: 420px;
    margin: -260px auto;
    width:80%;
}

.firstB .left{
    padding: 0px 10px;
    margin-top: 50px;
}
.firstB .left p{
    font-size: 14px;
    padding:0px 60px;
}


.firstB .left .btn{
    margin-bottom: 20px;
}

  }

@media (min-width: 900px) and (max-width: 1400px){
    .firstB .left h1{
        font-size:25px;
        width:250px;
    }
    .firstB .left p{
        font-size: 14px;
    }
    .firstB .fright img{
        margin-right:-200px;
    }
    .firstB .left{
        padding: 0px 0px 0px 120px;
    }
}



